<template>
    <div class="phpdoc-app" :style="style">
        <!--custom content-->
        <c-centered v-if="!file || loading">
            <c-loader></c-loader>
        </c-centered>
        <div class="row" v-if="file && !loading">
            <div class="col-md-12">
                <c-drag-zone class="zone">

                    <!--Tree-->
                    <div class="item" v-scrollable="height">
                        <pd-tree ref="tree"
                                 :query="entity.full_name"
                                 v-on:select="onTreeSelect"
                        ></pd-tree>
                    </div>

                    <!--Handle-->
                    <c-drag-zone-handle class="handle" v-on:change="saveZoneWidths"></c-drag-zone-handle>

                    <!--Entity - Method List - Property List -->
                    <div class="item" v-scrollable="height">

                        <pd-entity ref="entity"
                                   :query="entity.full_name"
                        ></pd-entity>

                        <div class="tabbable phpdoc-tabs">
                            <ul role="tablist" class="nav nav-tabs phpdoc-tabs-nav">
                                <li role="presentation"
                                    class="nav-item"
                                    :class="{ 'active' : isActive('methods') }">
                                    <a href="javascript:;"
                                       class="nav-link"
                                       @click.prevent="setActive('methods')"
                                       aria-controls="phpdoc-methods"
                                       role="tab"
                                    >Methods</a>
                                </li>

                                <li role="presentation"
                                    v-if="file.type !== 'interface'"
                                    class="nav-item"
                                    :class="{ 'active' : isActive('properties') }">
                                    <a href="javascript:;"
                                       class="nav-link"
                                       @click.prevent="setActive('properties')"
                                       aria-controls="phpdoc-properties"
                                       role="tab"
                                    >Properties</a></li>

                                <li role="presentation"
                                    class="nav-item"
                                    :class="{ 'active' : isActive('source') }">
                                    <a href="javascript:;"
                                       class="nav-link"
                                       @click.prevent="setActive('source')"
                                       aria-controls="phpdoc-source"
                                       role="tab"
                                    >Source</a></li>
                            </ul>
                            <div class="tab-content" ref="tabContent">

                                <div role="tabpanel" class="tab-pane phpdoc-tab-methods" :class="{ 'active' : isActive('methods') }">
                                    <div class="tabbable tabs-left">
                                        <pd-list-settings-menu list="methodList" type="methods">

                                            <pd-method-list
                                                    ref="methodList"
                                                    :query="entity.full_name"
                                                    use-signature
                                                    signature-hide-modifiers
                                                    signature-hide-argument-types
                                                    signature-hide-argument-defaults
                                                    signature-hide-argument-return
                                                    signature-hide-return
                                                    selectable
                                                    store-settings
                                                    v-on:select="onMethodSelect"
                                            ></pd-method-list>
                                        </pd-list-settings-menu>
                                        <p class="text-sm-right pr-sm fs-10">Right-click the methods for more options</p>
                                    </div>
                                </div>

                                <div role="tabpanel" class="tab-pane phpdoc-tab-properties" :class="{ 'active' : isActive('properties') }">
                                    <div class="tabbable tabs-left">
                                        <pd-property-list
                                                ref="propertyList"
                                                :query="entity.full_name"
                                        ></pd-property-list>
                                    </div>
                                </div>


                                <div role="tabpanel" class="tab-pane phpdoc-tab-source">
                                    <!--:class="{ 'active' : isActive('source') }"-->

                                </div>
                            </div>
                        </div>
                    </div>

                    <!--Handle -->
                    <c-drag-zone-handle class="handle" v-on:change="saveZoneWidths"></c-drag-zone-handle>

                    <!--Method-->
                    <div class="item" v-scrollable="height">
                        <pd-method
                                ref="method"
                                v-if="content === 'method' && methodQuery"
                                :query="methodQuery"
                        ></pd-method>
                        <pd-source
                                ref="source"
                                v-if="content === 'source'"
                                :query="entity.full_name"
                        >

                        </pd-source>
                    </div>
                </c-drag-zone>
            </div>
        </div>
    </div>
</template>


<script>
    import {mapGetters, mapActions} from 'vuex'
    import S from 'string'
    import {component} from 'phpdoc/mixins'
    import {resize} from 'codex/mixins'


    export default {
        name    : 'pd-app',
        mixins  : [component, resize],
        props   : {
            query : String,
            height: Number

        },
        data(){
            return {
                methodName : false,
                methodQuery: false,
                file       : false,
                loading    : true,

                active           : 'methods',
                content: 'method',
                selectedProperty : false,
                selectedMethod   : false,
                hasSlimScroll    : false,
                slimScrollTimeout: 0
            }
        },
        mounted(){
            if ( this.query )
                this.handleQuery(this.query);
            else
                this.getPhpdocEntities().then((entities) => this.handleQuery(this.entities[0].full_name));


            this.$$ready(() => setTimeout(() => {
                this.loading = false
                this.$nextTick(() => this.loadZoneWidths());
            }, 500));

        },
        watch   : {
            query(val){
                this.handleQuery(val);
            }
        },
        computed: {
            entities(){
                return Vue.codex.phpdoc.entities;
            },
            entity(){
                return this.file.entity;
            },
            style(){
                let styles = {};
                if ( this.height ) {
                    styles['height'] = this.height + 'px';
                }
                return styles;
            }
        },
        methods : {
            ...mapActions(['getPhpdocEntities']),
            handleQuery(query){
                let parsed = this.$phpdoc.parseRef(query);
                return this.getEntity(parsed.entityName).then(file => {
                    this.file = file;
//
                    let method = parsed.isMethod ? parsed.methodName : false;
                    if ( file.entity.methods.length ) {
                        method = file.entity.methods[0].name;
                    }
                    this.setMethod(method);
                })
            },
            setMethod(methodName){
                this.methodName = methodName;
                if ( methodName === false ) {
                    return this.methodQuery = false;
                }
                methodName = S(methodName).ensureRight('()')
                this.methodQuery = `${this.entity.full_name}::${methodName}`
            },
            onPropertySelect(propertyName){
                this.selectedProperty = propertyName;
            },
            onMethodSelect(methodName){
                this.selectedMethod = methodName;
                this.setMethod(methodName)
            },
            setActive(name){
                this.active = name;
                this.content = name === 'source' ? name : 'method';
                this.$emit('open', name);
                this.$root.$emit('resize')
            },
            isActive(name){
                return this.active === name;
            },

            onTreeSelect(query){
                this.$root.open(query);
            },
            saveZoneWidths(){
                let zones = []
                this.$$el.find('.drag-zone > .item').each(function (i) {
                    zones.push($(this).css('width'));
                })
                this.$cookie.set('phpdoc.zoneWidths', zones.join('-'))
                this.$events.$emit('phpdoc:zoneWidths', zones.join('-'))
            },
            loadZoneWidths(){
                let zoneWidths = this.$cookie.get('phpdoc.zoneWidths');
                if(!zoneWidths) return;

                if ( ! zoneWidths.toString().length ) return;
                let zones = zoneWidths.split('-')
                this.$$el.find('.drag-zone > .item').each(function (i) {
                    $(this).css('width', zones[i]);
                })
            },
            showSource(){

            }
        }

    }
</script>




