<template>
    <div class="phpdoc-method-signature" v-if="method">
        <span v-show="show.modifiers" v-if="method.static" class="phpdoc-modifier-static">static&nbsp;</span><span
            v-show="show.modifiers"
            :class="['phpdoc-visibility-' + method.visibility]">{{ method.visibility }}&nbsp;</span><span
            v-show="show.modifiers"
            v-if="method.abstract"
            class="phpdoc-modifier-abstract">abstract&nbsp;</span>{{ method.name }}&nbsp;<strong>(</strong>
        <span v-show="show.arguments" v-for="(argument, argumentIndex) in method.arguments">
            <strong v-if="argumentIndex !== 0">,</strong>
            <pd-type v-show="show.argumentTypes" :type="argument.types" :no-tooltip="noTooltip"></pd-type>
            <span class="color-cyan-900">{{ argument.name }}</span>
            <span v-show="show.argumentDefaults" class="color-grey-700 fs-10" v-if="argument.default.length > 0"> = {{ argument.default }}</span>
        </span>
        <strong>)</strong>
        <span v-show="show.return" v-if="method.returns && method.returns.length > 0">{{ returnCharacter }}</span>
        <pd-type v-show="show.return" v-if="method.returns" :type="method.returns" :no-tooltip="noTooltip"></pd-type>
    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex';
    import {find} from 'lodash'
    import {filter} from 'lodash'
    import {parseFullName} from 'phpdoc/utils'
    import {methodRefComponent} from 'phpdoc/mixins'

    export default {
        name    : 'pd-method-signature',
        mixins: [methodRefComponent],
        props   : {
            // ex: \Codex\Codex::get()
            hideModifiers       : Boolean,
            hideReturn          : Boolean,
            hideArguments       : Boolean,
            hideArgumentTypes   : Boolean,
            hideArgumentDefaults: Boolean,
            noTooltip           : Boolean,
            returnCharacter     : {type: String, default: '=>'}
        },
        mounted(){

        },
        watch   : {
        },
        methods : {
        },
        computed: {
            show(){
                return {
                    'modifiers'       : ! this.hideModifiers,
                    'return'          : ! this.hideReturn,
                    'arguments'       : ! this.hideArguments,
                    'argumentTypes'   : ! this.hideArgumentTypes,
                    'argumentDefaults': ! this.hideArgumentDefaults,
                }
            }
        }
    }
</script>


