<template>
    <div class="phpdoc-source">
    </div>
</template>
<script type="">
    import {fileRefComponent} from 'phpdoc/mixins'

    export default {
        name    : 'pd-source',
        mixins  : [fileRefComponent],
        mounted(){
            this.$$ready(() => {

                if ( this.file ) this.setSourceCode(this.file.source);
            })
        },
        watch   : {
            file(){
                this.$nextTick(() => this.setSourceCode(this.file.source));
            }
        },
        computed: {
            $$lineNumbers(){
                return this.$$el.find('.line-numbers-rows > span');
            }
        },
        methods : {
            setSourceCode(code){
                /** @type JQuery */
                var $sp   = this.$$el,
                    $pre  = this.$$('<pre class="language-php line-numbers"></pre>'),
                    $code = this.$$('<code class="language-php"></code>');

                $sp.html('');
                $pre.appendTo($sp);
                $code.appendTo($pre).text(code);
                Prism.highlightAll();
            },


            showSourceLine(line){
                this.$$el.parent().animate({ scrollTop: this.$$lineNumbers[line - 1].offsetTop });
            },
        }
    }
</script>




