<template>
    <div class="phpdoc-method">
        <c-centered v-if="!method">
            <c-loader></c-loader>
        </c-centered>
        <pd-method-signature v-if="method" :query="method.full_name" v-show="show.signature"></pd-method-signature>
        <div v-if="method" class="method-details">

            <h4 class="method-block-title" v-if="has.description" v-show="show.description">Description</h4>
            <div v-if="has.description" v-show="show.description" class="method-block">
                <div v-html="method.description"></div>
                <div v-if="has['long-description']" v-html="method['long-description']"></div>
            </div>

            <h4 class="method-block-title" v-if="tags.has('example')">Example</h4>
            <div v-if="tags.has('example')" class="method-block">
                <pre class="language-php line-numbers"><code v-html="tags.get('example').description"></code></pre>
            </div>

            <h4 class="method-block-title" v-if="has.tags" v-show="show.tags">Tags</h4>
            <div class="method-block" v-if="has.tags" v-show="show.tags">
                <pd-tags :tags="method.tags" :exclude="excludeTags"></pd-tags>
            </div>

            <h4 class="method-block-title" v-if="has.arguments" v-show="show.arguments">Arguments</h4>
            <ol class="method-block method-arguments" v-if="has.arguments" v-show="show.arguments">
                <li v-for="argument in method.arguments">
                    <div class="method-argument">
                        <span v-for="type in argument.types">
                            <span v-if="$index > 0">|</span>
                            <pd-type :type="type" :fqn="false"></pd-type>
                        </span>
                        <span class="color-cyan-900">&nbsp;{{ argument.name }}</span>
                        <span v-if="argument.default.length > 0"> = {{ argument.default }}</span>
                    </div>
                    <div v-if="argument.description && argument.description.length > 0" class="method-block" v-html="">
                        <span v-html="argument.description"></span>
                        <div v-if="argument['long-description'] && argument['long-description'].length > 0" v-html="argument['long-description']"></div>
                    </div>
                </li>
            </ol>


            <h4 class="method-block-title" v-if="has.returns" v-show="show.returns">Returns</h4>
            <div class="method-block" v-if="has.returns" v-show="show.returns">
                <pd-type :type="method.returns" v-on:click="onTypeClick"></pd-type>
                <!--<c-phpdoc-type v-for="(type, index) in method.returns" :type="type" :fqn="false"></c-phpdoc-type>-->
            </div>


        </div>
    </div>
</template>


<script>
    import {mapGetters, mapActions} from 'vuex';
    import {find} from 'lodash'
    import {filter} from 'lodash'
    import {Tags} from 'phpdoc/utils'
    import {methodRefComponent} from 'phpdoc/mixins'

    export default {
        name    : 'pd-method',
        mixins  : [methodRefComponent],
        props   : {
            hide: {type: String, default: ''},
        },
        data(){
            return {
                excludeTags: ['param', 'example', 'return'],
                tags       : new Tags
            }
        },
        mounted(){

        },
        watch   : {
            method(newVal){
                if ( this.hasData(this.method.returns) ) {
                    this.method.returns = this.method.returns.map((returns) => {
                        if ( returns === 'static' )
                            return this.method.class_name;
                        return returns;
                    })
                }
                this.$events.$emit('phpdoc:method:change', newVal);
                this.tags = (new Tags(newVal.tags)).exclude(this.excludeTags);

                setTimeout(() =>
                    this.$nextTick(() => {
                        if ( window['Prism'] ) window['Prism'].highlightAll();
                    }), 510)
            }
        },
        computed: {
            has(){
                let m = this.method;
                return {
                    tags              : this.hasData(this.filterTags(m.tags)),
                    returns           : this.hasData(m.returns),
                    description       : this.hasData(m.description),
                    'long-description': this.hasData(m['long-description']),
                    arguments         : this.hasData(m.arguments),
                }
            },
            show(){
                let hidden = this.hide.split(',');
                let blocks = ['signature', 'description', 'example', 'tags', 'arguments', 'returns'];
                let show = {};
                blocks.forEach(block => show[block] = hidden.indexOf(block) === - 1);
                return show;
            },
        },
        methods : {
            hasData(what){
                return what !== undefined && what.length > 0
            },
            filterTags(tags){
                return filter(tags, (tag) => {
                    return this.excludeTags.indexOf(tag.name) === - 1;
                })
            },
            onTypeClick(){

            }
        }
    }
</script>


