<template>
    <div class="phpdoc-entity">
        <c-centered v-if="!file">
            <c-loader></c-loader>
        </c-centered>
        <div v-if="file">
            <header class="phpdoc-header">
                <i :class="'phpdoc-type-' + file.type"></i>
                <h3 class="header-title">
                    <span :class="'phpdoc-type-' + file.type">{{ entity.full_name | removeStartSlash}}</span>
                    <small v-if="hasExtend" class="pl-xs fs-13">extends</small>
                    <pd-type v-if="hasExtend" :type="entity.extends" class="fs-13"></pd-type>
                </h3>
            </header>

            <!-- DESC-->
            <div class="phpdoc-block">
                <p v-if="hasDescription" class="fs-13" v-html="entity.description"></p>
                <pd-tags :object="entity" :exclude="['example', 'inherited_from']"></pd-tags>
            </div>

            <div class="phpdoc-block">
                <pd-tags :tags="entity.tags" :exclude="['example', 'inherited_from']"></pd-tags>
            </div>
            <div class="phpdoc-block" v-if="withProperties">
                <pd-property-list :query="query" compact></pd-property-list>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex';
    import 'jquery-slimscroll'
    import {fileRefComponent} from 'phpdoc/mixins';

    export default {
        name    : 'pd-entity',
        mixins  : [fileRefComponent],
        props   : {
            maxHeight     : {type: Number, default: 0},
            withProperties: Boolean,
            withMethods   : Boolean,
            extended      : Boolean
        },
        data(){
            return {
                active: 'methods'
            }
        },
        mounted(){
        },
        watch   : {},
        computed: {
            hasExtend()            {
                return this.entity.extends && this.entity.extends.length > 0;
            },
            hasDescription()            {
                return this.entity.description.length > 0;
            },
        },
        methods : {}
    }

</script>




