<template>
    <!--Methods List-->
    <ul class="phpdoc-property-list" v-if="file" :style="scrollStyle" :class="{ 'compact' : compact }">
        <li v-for="property in filteredProperties" :class="liClass.concat(active === property.name ? ['active']:[])">

            <a @click.prevent.stop="onPropertyClick(property)" href="javascript:;" class="no-hover no-click" :class="aClass">
                <i v-if="useIcon" class="pr-xs" :class="'phpdoc-visibility-' + property.visibility"></i>
                <span v-show="!useIcon" :class="'phpdoc-visibility-' + property.visibility">{{ property.visibility }}</span>
                {{ property.name }} :
                <pd-type :type="property.types" no-tooltip></pd-type>
                <span class="description" v-html="property.description"></span>
            </a>

            <!-- inherited -->
            <a v-if="property.inherited"
               @click.prevent.stop="$emit('inheritedClick', property)"
               class="nav-link nav-link-modifier nav-link-modifier-big no-click"
               href="javascript:;">
                <c-popover popover-class='popover-phpdoc' trigger='hover' placement="right" effect="fade">
                    <i class="phpdoc-modifier-inherited"></i>
                    <span slot="content">
                        Inherited from: <br>
                        <pd-type :type="property.full_name" full-name no-tooltip></pd-type>
                    </span>
                </c-popover>
            </a>

            <!-- static -->
            <a v-if="property.static"
               class="nav-link nav-link-modifier no-hover no-click"
               title="Static"
               href="javascript:;">
                <i class="phpdoc-modifier-static"></i>
            </a>


        </li>
    </ul>
</template>

<script>
    import Vue from 'vue';
    import {mapGetters, mapActions} from 'vuex';
    import {chain, find} from 'lodash';
    import {fileRefComponent} from 'phpdoc/mixins'


    export default {
        name    : 'pd-property-list',
        mixins  : [fileRefComponent],
        props   : {
            itemClass      : {type: String, default: 'nav-item'},
            linkClass      : {type: String, default: 'nav-link'},
            exclude        : {type: Array, default: () => []},
            hide           : {type: String, default: ''},
            selectable     : {type: Boolean, default: false},
            compact        : Boolean,
            useIcon        : Boolean,
            persistSettings: Boolean,
            mergeSettings  : Object
        },
        data(){
            return {
                active     : undefined,
                settingsKey: 'pd-property-list-setting',
                settings   : {
                    show: {
                        static   : true,
                        inherited: false,
                        final    : true,
                        public   : true,
                        protected: true,
                        private  : true
                    },
                    sort: {
                        by : '',
                        dir: 'asc'
                    },
                    view: ''
                }
            }
        },
        mounted(){
            this.loadSettings();
            if ( this.mergeSettings ) {
                this.recursiveSetter(this.settings, this.mergeSettings);
            }
        },
        watch   : {
            settings: {
                deep: true,
                handler(val, old){
                    this.saveSettings(val);
                }
            }
        },
        computed: {
            properties(){
                return this.entity.properties;
            },
            filteredProperties(){
                return this.filter(this.properties)
            },
            liClass(){
                return this.itemClass.split(' ');
            },
            aClass(){
                return this.linkClass.split(' ');
            }
        },
        methods : {
            saveSettings(settings){
                if ( ! this.persistSettings ) return;
                this.$cookie.set(this.settingsKey, JSON.stringify(settings || this.settings))
            },
            loadSettings(){
                if ( ! this.persistSettings ) return;
                let parsed = JSON.parse(this.$cookie.get(this.settingsKey))
                if ( parsed !== null ) {

                    this.recursiveSetter(this.settings, parsed);
                }
            },
            // make sure only to overide keys that exist, not to add in bad keys after version changes
            recursiveSetter(current, loaded){
                Object.keys(current).forEach(key => {
                    if ( typeof current[key] === 'object' ) {
                        if ( typeof loaded[key] !== 'undefined' ) {

                            this.recursiveSetter(current[key], loaded[key]);
                        }
                    } else {
                        if ( typeof loaded[key] !== 'undefined' ) {
                            current[key] = loaded[key];

                        }

                    }
                })
            },
            filter(properties) {
                let items = chain(properties);

                if ( this.settings.show.inherited === false ) {
                    items = items.filter((item) => item.inherited !== true);
                }
                if ( this.settings.show.public === false ) {
                    items = items.filter((item) => item.visibility !== 'public');
                }
                if ( this.settings.show.protected === false ) {
                    items = items.filter((item) => item.visibility !== 'protected');
                }
                if ( this.settings.show.private === false ) {
                    items = items.filter((item) => item.visibility !== 'private');
                }

                items = items.sortBy(this.settings.sort.by);

                if ( this.settings.sort.dir === 'desc' ) {
                    items = items.reverse();
                }

                return items.value();
            },
            setActive(propertyName){
                if ( ! this.selectable ) return;
                this.active = propertyName;
                this.$emit('select', propertyName)
            },
            isActiveProperty(property){
                if ( ! this.selectable ) return false;
                return this.active === property;
            },
            onPropertyClick(property){
                this.$emit('click', property)
                this.setActive(property.name);
            }
        }
    }
</script>


