<template>
    <div class="phpdoc-tags">
        <table v-if="filteredTags.length > 0" class="table table-tags">
            <tbody>
            <tr v-for="tag in filteredTags">
                <th width="150" valign="middle">{{ tag.name }}</th>
                <td :colspan="tag.type ? 1 : 2"><span v-if="tag.description.length > 0" v-html="tag.description"></span></td>
                <td v-if="tag.type">
                    <pd-type :type="tag.type"></pd-type>
                </td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    import {FullyQualifiedNameParser} from 'phpdoc/utils'
    import {filter, chain} from 'lodash'

    /**
     * Tags can be found on Entities, Methods and Properties.
     * We use a Fully Qualified Name as property to indicate where we want to look for tags
     */
    export default {
        name    : 'pd-tags',
        props   : {
            tags   : {type: Array, default: () => []},
            exclude: {type: Array, default: []}
        },
        data(){
            return {res: false}
        },
        mounted(){
        },
        watch   : {},
        computed: {
            filteredTags(){
                if ( this.tags.length === 0 ) return [];

                return filter(this.tags, (tag) => {
                    return this.exclude.indexOf(tag.name) === - 1;
                });
            }
        },
        methods : {}
    }
</script>




