<template>
    <span>
        <span v-for="(type, itype) in types" >

            <span v-if="type.isPrimitive" :class="type.cssClass">{{ type.name }}</span>

            <!--'#!/' + type.type-->
            <a v-if="type.isEntity && showTooltip === false"
               href="javascript:;"
               @click.prevent.stop="onEntityClick(type)"
               :class="type.cssClass"
            >{{ fullName ? type.fullName : type.name }}</a>


            <c-popover  v-if="type.isEntity && showTooltip === true"  popover-class='popover-phpdoc' trigger='hover' placement="top" effect="fade">
                <a href="javascript:;" :class="type.cssClass" @click.prevent.stop="onEntityClick(type)">{{ fullName ? type.fullName : type.name }}</a>
                <span slot="content" :class="type.cssClass">
                    {{ type.fullName }}
                </span>
            </c-popover>

            <span v-if="seperator.length > 0" v-show="itype < types.length -1" class="type-seperator">{{ seperator  }} </span>
        </span>
    </span>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex';
    import {removeStartSlash, Type} from 'phpdoc/utils'
    import {find, last, isArray, isString} from 'lodash' //, isNumber, isObject, isUndefined, isBoolean, isNull


    export default {
        name    : 'pd-type',
        props   : {
            type       : {type: [String, Array], default: []},
            fullName   : {type: Boolean, default: false},
            noTooltip: {type: Boolean, default: false},
            tooltip    : {
                type: Object, default(){
                    return {effect: 'scale', placement: 'top'}
                }
            },
            seperator  : {type: String, default: '|'},
            static: String
        },
        data(){
            return {
                types     : [],
                isInternal: false
            }
        },
        beforeMount(){
            return this.getPhpdocEntities();
        },
        mounted(){
                if ( this.type ) this.types = this.parseType(this.type);
        },
        watch   : {
            type(newVal){
                this.types = this.parseType(newVal);
            },
            entities(){
                this.types = this.parseType(this.type);
            }
        },
        methods : {
            ...mapActions(['getPhpdocEntities']),
            parseType(type){
                if ( isString(type) ) {
                    type = type.split('|');
                }
                if ( isArray(type) ) {
                    type = type.map(t => new Type(t.toString(), this));
                }
//
                return type;
            },
            onEntityClick(type){
                this.$emit('click', type);
                this.$events.$emit('phpdoc:type:click', type);
            }
        },
        computed: {
            ...mapGetters(['entities']),
            showTooltip(){
                return this.noTooltip === false;
            }
        }
    }
</script>


